/* home drop-down */



a {
  text-decoration: none;
  color: #000000;
}

a:hover {
  color: #222222;
  text-decoration: none;
}

.panel-heading {
  border: 1px solid;
  width: 280px;
  border-radius: 5px;
}


/* abhay */
div#faqhead3 {
  display: -webkit-flex;
  display: flex;
}

#main {
  margin: 50px 0;
}

#main #faq .card {
  margin-bottom: 30px;
  border: 0;
}

span.btn.btn-header-link.d-flex.justify-content-between,
span.btn.btn-header-link.collapsed.d-flex.justify-content-between {
  border: 1px solid;
  width: 200px !important;
}

a.btn.btn-header-link.d-flex.justify-content-between:focus,
a.btn.btn-header-link.collapsed.d-flex.justify-content-between:focus {
  outline: none;
  box-shadow: none
}

#main #faq .card .collapsing {
  background: #FFE472;
  line-height: 30px;
}

/* #main #faq .card .collapse {
  border: 0;
} */

#main #faq .card .collapse.show {
  background: #FFE472;
  line-height: 30px;
  color: #222;
}

